.title {
  margin-top: 1ch;
}

.horizontal {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 0px;
}

.horizontal:last-child {
  padding-bottom: 16px;
}

.main-icon {
  align-self: center;
  margin-left: 1em;
  margin-right: 0.5em;
}

.summary > div {
  align-items: center;
}

.summary .icon {
  margin-right: 1ch;
}

.details {
  display: inherit;
}

.details > *:not(:last-child) {
  margin-bottom: 1em;
}

.details code {
  background: #eee;
  border-radius: 0.25ch;
  padding: 0.5ch 0.75ch;
}
